body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  padding:3%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  font-size:14px;
}

.appLoading{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.movieCard{
  background-color:white;
  width:40%;
  display: flex;
  align-items:flex-start;
  flex-direction: column;
  margin-bottom:50px;
  padding:0 20px;
  box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133,0.22);
}

.movieTitle h1{
  font-size:20px;
  font-weight: 600;
}

.movieSummary {
  text-overflow: ellipsis;
  color:#B4B5BD;
  overflow: hidden;
}

.posterImg{
  max-width: 100%;
  position: relative;
  top:-20px;
  box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80,80,80,0.22);
}

@media screen and (min-width:320px) and (max-width:667px){
  .movieCard{
      width:100%;
  }
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait){
  .movieCard{
      width:100%;
      flex-direction: column;
  }
  .posterImg{
      top:0;
      left:0;
      width:100%;
  }
}
